%circle-icon {
  display: block;
  border-radius: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding: 0;
}

@mixin circle-icon($icon, $diameter: 40px, $font-size: ($diameter/2), $bg-color: #b2b2b2, $color: invert($bg-color), $copy: false) {
  & {
    @if not ($copy) {
      @extend %circle-icon;
      height: $diameter;
      width: $diameter;
      line-height: $diameter;
      background-color: $bg-color;
      font-size: $font-size;
      color: $color;
    }
    @if ($icon == false) {
    } @else {
      @at-root .#{$icon} {
        @extend %#{$icon};
      }
    }
  }
}


