// Hover Mixins

@mixin hover {
  &:hover {
    @content
  }
}

@mixin hover-focus {
  &:focus,
  &:hover {
    @content
  }
}

@mixin plain-hover-focus {
  &,
  &:focus,
  &:hover {
    @content
  }
}

@mixin focus-active {
  &:focus,
  &:active {
    @content
  }
}

@mixin hover-focus-active {
  &:focus,
  &:active,
  &:hover {
    @content
  }
}

@mixin hover-focus-active-class {
  &:focus,
  &:active,
  &:hover,
  &.active {
    @content
  }
}