%btn-box {
  display: inline-block;
  position: relative;
  z-index: 999;
  margin: 0;
  padding: 20px 45px;

  text-decoration: none;
  font-weight: 600;
  font-size: .8em;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .4em;
  transition: .2s;
  transition-delay: .1s;
  &::after {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    top: 50%;
    left: -1000%;
    height: 2px;
    width: 600%;
    margin-top: -1px;
    background-color: #44B5D5;
    transition: .5s;
  }
  & span {
    display: block;
    position: absolute;
    z-index: -1;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    overflow: hidden;
    &::before {
      content: "";
      display: block;
      position: absolute;
      top: -5%;
      left: -210%;
      width: 200%;
      height: 110%;
      background-color: #44B5D5;
      transform: skew(-24deg, 0);
      transition: .5s;
    }
  }
  &:hover {
    padding: 20px 60px;
    letter-spacing: .7em;
    color: #fff;
    &::after {
      left: -10%;
      width: 120%;
    }
    & span::before {
      left: -25%;
    }
  }
}

@mixin btn-box($color) {
  @extend %btn-box;
  border: 4px solid $color;
  color: $color;
  &::after {
    background-color: $color;
  }
  & span {
    &::before {
      background-color: $color;
    }
  }
}