.contact {
  background: url(./assets/img/pattern-bg/paper_light.jpg) $color-light2;;
  &__bg-img {
    @extend .home__bg-img;
    bottom: -20%;
    right: -15%;
    max-width: 50%;
    transform: rotate(160deg);
  }
  &__content {
    @extend .home__content;
    max-width: 1000px;
  }
}

.contact-form {
  @extend %row, %pos-x-center-xs;
  margin-top: 15px;
  &__input {
    @extend %xs-12, %sm-6;
    @include form-input(chisato, $color-green, $color-dark);
    letter-spacing: 1px;
    padding-bottom: 10px;
  }
  &__textarea {
    @extend .contact-form__input;
    padding-bottom: 30px;
    flex: 0 0 100%;
    max-width: 100%;
  }
  &__btn {
    @include btn-box($color-green);
    background: transparent;
    border-width: 2px;
  }
  &__submit {
    width: 100%;
    height: 100%;
    opacity: 0;
  }
  &__feedback {
    padding-top: 30px;
    min-height: 62px;
  }
}
