.webdesign-page {
  position: relative;
  min-height:100vh;
  background: #fff;
  z-index: -8;
  &__section {
    &--top {
      &__bg-img {
        display: block;
        position: absolute;
        z-index: -1;
        bottom: 0;
        right: 0;
        width: 100%;
        max-width: 50%;
      }

    }

  }
}




.services-page {

  &__bg-img {
    @extend .fadeInRight; //scroll
    display: block;
    position: absolute;
    z-index: -1;
    bottom: 0;
    right: 0;
    width: 100%;
    max-width: 50%;
  }
  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 1100px;
    padding: 15px;
    margin: 0;
    transform: translate(-50%, -50%);
  }
  &__heading {
    @extend .fadeInUp; //scroll

    &--name {
      color: $color-pink;
      font-style: normal;
      font-size: 1em;
      letter-spacing: -5px;
    }
  }
  &__sub-heading {
    position: relative;
    padding: 1em 0 1em;
    color: $color-dark;
    font-family: Lora, serif;
    font-size: 1.15em;
    line-height: 1.75em;
  }
  &__offer {
    @extend .fadeInUp; //scroll
    @extend %row, %pos-x-center-xs;
    text-align: center;
    padding: 20px;
  }
  &__btn {
    @include btn-box($color-pink);
  }
}


.service-card {
  @extend %xs-12, %sm-4;
  padding-bottom: 10px;
  &__heading {
    position: relative;
    font-size: 1.25em;
    font-weight: 700;
    letter-spacing: -0.05em;
  }
  &__list {
    position: relative;
    color: $color-dark;
    font-family: Lora, serif;
    line-height: 1.75em;
    list-style-type: none;
    font-size: 0.875em;
    display: none;
    cursor: pointer;
    &.active {
      display: block;
      cursor: default;
    }
  }
  &__icon {
    @extend %icon--md-caret-right;
    position: absolute;
    top: 2px;
    color: $color-pink;
    right: 15px;
    display: none;
    transition: 0.1s;
    &.active {
      display: block;
    }
  }
}
