.home {
  &__bg-img {
    display: block;
    position: absolute;
    z-index: -1;
    bottom: 0;
    right: 0;
    width: 100%;
    max-width: 60%;
  }
  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 1100px;
    padding: 7%;
    margin: 0;
    transform: translate(-50%, -50%);
    text-align: center;
  }
   &__btn {
    @include btn-box($color-blue);
     margin-top: 15px;
  }
}

