
.logo {
  display: block;
  position: fixed;
  z-index: 2000;
  top: 15px;
  left: 15px;
  width: 50px;
  height: 50px;

  text-align: center;
  line-height: 60px; /*Same as height;*/

  &__img {
    margin: 15%;
    width: 70%;
  }
  &__animation {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    border: 1px solid #323739;
    opacity: 0.9;
    &:nth-child(2) {
      transform: rotate(6deg);
      transition: 0.2s;
    }
    &:nth-child(3) {
      transform: rotate(19deg);
      transition: 0.4s;
    }
  }
  &__link {
    display: block;
    height: 100%;
    @include hover-focus-active {
      .logo__animation {
        &:nth-child(2) {
          animation: rotation1 3s 1;

          border-color: $color-blue;
        }
        &:nth-child(3) {
          animation: rotation2 3s 1;
          animation-delay: 0.2s;
          border-color: $color-turquoise;
        }
      }
    }
  }
}

@include break(md) {
  .logo {
    width: 60px;
    height: 60px;
    margin: 0;
    top: 30px;
    left: 30px;
  }
}

@include break(lg) {
  .logo {
    width: 75px;
    height: 75px;
    top: 45px;
    left: 45px;
    &__animation {
      border: 2px solid #323739;
    }
  }
}
