html {
  box-sizing: border-box;
  font-size: 16px;
  -ms-overflow-style: scrollbar;
  // Changes the default tap highlight to be completely transparent in iOS.
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*, ::after, ::before {
  box-sizing: inherit;
}

@-ms-viewport {
  width: device-width
}

// TODO - Check if this is needed, or if the fix is too harsh

figure {
  margin: 0
}

a:active, a:hover {
  outline: 0
}

img {
  border: 0
}

svg:not(:root) {
  overflow: hidden
}

legend {
  border: 0;
  padding: 0
}