body {
  font-family: Metropolis, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.75;
  background-color: #fff;
  color: #252627;
  font-size: 0.75rem; /*  Master Font Size adjustment - 14px*/
  position: relative;
  z-index: -1;
  @include break(sm) {
    font-size: 1rem; /*  Master Font Size adjustment - 16px*/
  }
}

strong {
  font-weight: 700;
}

.thin {
  font-weight: 200;
}

p {
  font-family: Lora, serif;
  font-size: 1.5em;
  > span {
    display: block;
    font-size: 0.875em;
    font-style: italic;
  }
  > small {
    display: block;
    font-style: italic;
    font-size: .666667em;
  }
}

ul {
  padding: 0;
  list-style: none;
  font-family: Lora, serif;
}

h1, h2, h3, h4, h5, h6 {
  position: relative;
  font-family: Metropolis, sans-serif;
  margin: 0;
  font-style: normal;
  line-height: 1;
  text-transform: uppercase;
  font-weight: 700;
  word-wrap: break-word;
  > span {
    font-family: Lora, serif;
    text-transform: none;
    display: block;
    font-style: italic;
    margin-top: .5em;
    font-weight: 400;
    line-height: 1;
  }
}

/*
h1 Used for Page Titles
h1 > em Used for title highlight
h1 > span Used for Page Subtitles
*/
h1 {
  text-align: center;
  font-weight: 800;
  letter-spacing: -.025em;
  font-size: 4em;
  @include break(md) {
    font-size: 5em;
  }
  @include break(lg) {
    font-size: 6.5em;
  }
  > em {
    font-style: normal;
    letter-spacing: 2px;
  }
  > span {
    letter-spacing: .1em;
    font-size: .3em;
    line-height: 1.3em;
    @include break(sm) {
      font-size: .225em;
    }
  }
}

/*
h2 used for Left & right aligned headings on Services page
h2 > span used for subtitles
*/
h2 {
  margin: 0.5em 0;
  font-size: 2.25em;
  @include break(md) {
    font-size: 2.85em;
  }
  > span {
    font-size: 1.25rem;
  }
}

/*
h3 used for section titles
*/
h3 {
  font-weight: 800;
  margin: 1em 0;
  font-size: 2.25em;
  flex: 0 0 100%;
  @include break(md) {
    font-size: 2.5em;
  }
  > span::before {
    content: "";
    display: block;
    width: 40px;
    height: 2px;
    background-color: $color-dark;
    position: absolute;
    bottom: -0.5em;
    left: 0;
  }
  > em {
    font-style: normal;
  }
}

/*
h3 used for Card Headings
*/
h4 {
  margin: 1.33em 0;
  font-size: 1.75em;
}

h5 {
}

h6 {
}

hr {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid #d3d3d3;
}

small {
  font-size: $font-small;
}

.xsmall {
  font-size: $font-xsmall;
}

nav {
  font-size: $font-nav-size;
  > ul {
    font-family: Metropolis, sans-serif;
  }
}

/*TODO Move to color base scss file*/
.blue {
  color: $color-blue;
  &::before {
    background-color: $color-blue;
  }
}

.green {
  color: $color-green;
  &::before {
    background-color: $color-green;
  }
}

.pink {
  color: $color-pink;
  &::before {
    background-color: $color-pink;
  }
}

.grey {
  color: $color-grey;
  &::before {
    background-color: $color-grey;
  }
}

.faded {
  opacity: 0.75;
}