@mixin form-input($style, $focus-color,$color: null, $bg-color: #f0f0f0) {
  @extend %input--#{$style};

  & input, & textarea {
    @extend %input__field;
    @extend %input__field--#{$style};
    resize: none;
    &:focus, &.active {
      @if ($focus-color) {
        border-color: $focus-color;
      }
    }
    @if ($color) {
      color: $color;
    }
  }
  & label {
    @extend %input__label;
    @extend %input__label--#{$style};
    & span {
      @extend %input__label-content;
      @extend %input__label-content--#{$style};
      @if ($color) {
        color: $color;
      }
      &::after {
        @if ($focus-color) {
          color: $focus-color;
        }
      }
    }
  }
}

/*Base Design*/

%input__field {
  position: relative;
  display: block;
  float: right;
  padding: 0.8em;
  width: 60%;
  border: none;
  border-radius: 0;
  background: #f0f0f0;
  color: #aaa;
  font-weight: 400;
  -webkit-appearance: none; /* for box shadows to show on iOS */
  &:focus {
    outline: none;
  }
}

%input__label {
  display: inline-block;
  float: right;
  padding: 0 1em;
  width: 40%;
  color: #6a7989;
  font-weight: bold;
  font-size: 70.25%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

%input__label-content {
  position: relative;
  display: block;
  padding: 1.6em 0;
  width: 100%;
}


/* Individual styles */

/* Chisato */
%input--chisato {
  padding-top: 1em;
}

%input__field--chisato {
  width: 100%;
  padding: 0.8em 0.5em;
  background: transparent;
  border: 2px solid;
  transition: border-color 0.3s;
  color: #b5b5b5;
  &:focus, &.active {
    border-color: #FF4985;
    & + label {
      animation: anim-chisato-1 0.3s forwards;
    }
    & + label span {
      animation: anim-chisato-2 0.3s forwards;
    }
  }
}

%input__label--chisato {
  width: 100%;
  position: absolute;
  top: 0;
  text-align: left;
  overflow: hidden;
  padding: 0;
  pointer-events: none;
  -webkit-transform: translate3d(0, 3em, 0);
  transform: translate3d(0, 3em, 0);

}

%input__label-content--chisato {
  padding: 0 1em;
  font-weight: 400;
  color: #b5b5b5;
  &::after {
    content: attr(data-content);
    position: absolute;
    top: -200%;
    left: 0;
    font-weight: 600;
    color: #da6484;
  }
}

@-webkit-keyframes anim-chisato-1 {
  0%, 50% {
    -webkit-transform: translate3d(0, 3em, 0);
    transform: translate3d(0, 3em, 0);
  }
  51%, 100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes anim-chisato-2 {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  50%, 51% {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    color: transparent;
    -webkit-transform: translate3d(0, 200%, 0);
    transform: translate3d(0, 200%, 0);
  }
}

/* Manami */
%input--manami {
  overflow: hidden;
}

%input__field--manami {
  width: 100%;
  background: transparent;
  padding: 0.5em;
  margin-bottom: 2em;
  color: #f48fb1;
  z-index: 100;
  opacity: 0;
  &:focus, &.active {
    opacity: 1;
    -webkit-transition: opacity 0s 0.3s;
    transition: opacity 0s 0.3s;
    & + label {
      &::before {
        transform: translate3d(0, 0, 0);
      }
      &::after {
        opacity: 0;
      }
    }
    & + label span {
      color: #cbc4c6;
      -webkit-transform: translate3d(0, 2.1em, 0) scale3d(0.65, 0.65, 1);
      transform: translate3d(0, 2.1em, 0) scale3d(0.65, 0.65, 1);
    }
  }
}

%input__label--manami {
  width: 100%;
  position: absolute;
  text-align: left;
  padding: 0.5em 0;
  pointer-events: none;
  font-size: 1em;
  &::before, &::after {
    content: '';
    position: absolute;
    width: 100%;
    left: 0;
  }
  &::before {
    height: 100%;
    top: 0;
    background: #f0f0f0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
  &::after {
    height: 2px;
    background: #6a7989;
    top: 100%;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
  }
}

%input__label-content--manami {
  padding: 0;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transition: -webkit-transform 0.3s, color 0.3s;
  transition: transform 0.3s, color 0.3s;
}


/* Haruki */

%input--haruki {
  margin: 4em 1em 1em;
}

%input__field--haruki {
  padding: 0.4em 0.25em;
  width: 100%;
  background: transparent;
  color: #bf3e5c;
  font-size: 1.55em;
  &:focus, &.active {
    & + label span {
      -webkit-transform: translate3d(0, -90%, 0);
      transform: translate3d(0, -90%, 0);
    }
    & + label {
      &::before {
        -webkit-transform: translate3d(0, -0.5em, 0);
        transform: translate3d(0, -0.5em, 0);
      }
      &::after {
        -webkit-transform: translate3d(0, 0.5em, 0);
        transform: translate3d(0, 0.5em, 0);
      }

    }
  }
}

%input__label--haruki {
  position: absolute;
  width: 100%;
  text-align: left;
  pointer-events: none;
  &::before, &::after {
    content: '';
    position: absolute;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 4px;
    background: #6a7989;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
  &::before {
    top: 0;
  }
  &::after {
    bottom: 0;
  }
}

%input__label-content--haruki {
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}

/* Hoshi */
%input--hoshi {
  overflow: hidden;
}

%input__field--hoshi {
  margin-top: 1em;
  padding: 0.85em 0.15em;
  width: 100%;
  background: transparent;
  color: #595F6E;
  &:focus, &.active {
    & + label {
      &::after {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }
    }
    & + label span {
      -webkit-animation: anim-1 0.3s forwards;
      animation: anim-1 0.3s forwards;
    }
  }
}

%input__label--hoshi {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0 0.25em;
  width: 100%;
  height: calc(100% - 1em);
  text-align: left;
  pointer-events: none;
  &::before, &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% - 10px);
    border-bottom: 1px solid #B9C1CA;
  }
  &::after {
    margin-top: 2px;
    border-bottom: 4px solid red;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    border-color: hsl(200, 100%, 50%);
  }
}

%input__label-content--hoshi {
  position: absolute;
}

@-webkit-keyframes anim-1 {
  50% {
    opacity: 0;
    -webkit-transform: translate3d(1em, 0, 0);
    transform: translate3d(1em, 0, 0);
  }
  51% {
    opacity: 0;
    -webkit-transform: translate3d(-1em, -40%, 0);
    transform: translate3d(-1em, -40%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, -40%, 0);
    transform: translate3d(0, -40%, 0);
  }
}

@keyframes anim-1 {
  50% {
    opacity: 0;
    -webkit-transform: translate3d(1em, 0, 0);
    transform: translate3d(1em, 0, 0);
  }
  51% {
    opacity: 0;
    -webkit-transform: translate3d(-1em, -40%, 0);
    transform: translate3d(-1em, -40%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, -40%, 0);
    transform: translate3d(0, -40%, 0);
  }
}

/* Kuro */
%input--kuro {
  max-width: 320px;
  margin-bottom: 3em;
}

%input__field--kuro {
  width: 100%;
  background: transparent;
  color: #9196A1;
  opacity: 0;
  text-align: center;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  &:focus, &.active {
    opacity: 1;
    -webkit-transition-delay: 0.3s;
    transition-delay: 0.3s;
    & + label {
      &::before {
        -webkit-transform: translate3d(-10%, 0, 0);
        transform: translate3d(-10%, 0, 0);
      }
      &::after {
        -webkit-transform: translate3d(10%, 0, 0);
        transform: translate3d(10%, 0, 0);
      }
    }
    & + label span {
      -webkit-animation: anim-2 0.3s forwards;
      animation: anim-2 0.3s forwards;
    }
  }
}

%input__label--kuro {
  position: absolute;
  left: 0;
  width: 100%;
  color: #df6589;
  pointer-events: none;
  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    border: 4px solid #747981;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
  &::before {
    border-right: none;
  }
  &::after {
    left: 50%;
    border-left: none;
  }
}

%input__label-content--kuro {
  visibility: inherit;
}

@-webkit-keyframes anim-2 {
  50% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 1);
    transform: scale3d(0.3, 0.3, 1);
  }
  51% {
    opacity: 0;
    -webkit-transform: translate3d(0, 3.7em, 0) scale3d(0.3, 0.3, 1);
    transform: translate3d(0, 3.7em, 0) scale3d(0.3, 0.3, 1);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 3.7em, 0);
    transform: translate3d(0, 3.7em, 0);
  }
}

@keyframes anim-2 {
  50% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 1);
    transform: scale3d(0.3, 0.3, 1);
  }
  51% {
    opacity: 0;
    -webkit-transform: translate3d(0, 3.7em, 0) scale3d(0.3, 0.3, 1);
    transform: translate3d(0, 3.7em, 0) scale3d(0.3, 0.3, 1);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 3.7em, 0);
    transform: translate3d(0, 3.7em, 0);
  }
}

/* Jiro */
%input--jiro {
  margin-top: 2em;
}

%input__field--jiro {
  padding: 0.85em 0.5em;
  width: 100%;
  background: transparent;
  color: #DDE2E2;
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  &:focus, &.active {
    opacity: 1;
    -webkit-transition-delay: 0.3s;
    transition-delay: 0.3s;

    & + label {
      &::before {
        -webkit-transition-delay: 0s;
        transition-delay: 0s;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }
      &::after {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        -webkit-transition-delay: 0.3s;
        transition-delay: 0.3s;
        -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
        transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
      }
    }
    & + label span {
      -webkit-transform: translate3d(0, -80%, 0);
      transform: translate3d(0, -80%, 0);
      -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
      transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
    }

  }
}

%input__label--jiro {
  position: absolute;
  left: 0;
  padding: 0 0.85em;
  width: 100%;
  height: 100%;
  text-align: left;
  pointer-events: none;
  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
  &::before {
    border-top: 2px solid #6a7989;
    -webkit-transform: translate3d(0, 100%, 0) translate3d(0, -2px, 0);
    transform: translate3d(0, 100%, 0) translate3d(0, -2px, 0);
    -webkit-transition-delay: 0.3s;
    transition-delay: 0.3s;
  }
  &::after {
    z-index: -1;
    background: #6a7989;
    -webkit-transform: scale3d(1, 0, 1);
    transform: scale3d(1, 0, 1);
    -webkit-transform-origin: 50% 0;
    transform-origin: 50% 0;
  }
}

%input__label-content--jiro {
  -webkit-transition: -webkit-transform 0.3s 0.3s;
  transition: transform 0.3s 0.3s;
}

/* Minoru */
%input--minoru {
  visibility: inherit;
}

%input__field--minoru {
  width: 100%;
  background: #fff;
  box-shadow: 0 0 0 2px transparent;
  color: #eca29b;
  -webkit-transition: box-shadow 0.3s;
  transition: box-shadow 0.3s;
  &:focus, &.active {
    box-shadow: 0 0 0 2px #eca29b;
    & + label {
      pointer-events: none;
      &::after {
        -webkit-animation: anim-shadow 0.3s forwards;
        animation: anim-shadow 0.3s forwards;
      }
    }
  }
}

%input__label--minoru {
  padding: 0;
  width: 100%;
  text-align: left;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 4em;
    box-shadow: 0 0 0 0;
    color: rgba(199, 152, 157, 0.6);
  }
}

%input__label-content--minoru {
  padding: 0.75em 0.15em;
}

@-webkit-keyframes anim-shadow {
  to {
    box-shadow: 0 0 100px 50px;
    opacity: 0;
  }
}

@keyframes anim-shadow {
  to {
    box-shadow: 0 0 100px 50px;
    opacity: 0;
  }
}

.input__label-content--minoru {
  padding: 0.75em 0.15em;
}

/* Yoko */
%input--yoko {
  visibility: inherit;
}

%input__field--yoko {
  z-index: 10;
  width: 100%;
  background: transparent;
  color: #f5f5f5;
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  &:focus, &.active {
    opacity: 1;
    -webkit-transition-delay: 0.3s;
    transition-delay: 0.3s;
    & + label {
      pointer-events: none;
      &::before {
        -webkit-transform: perspective(1000px) rotate3d(1, 0, 0, 0deg);
        transform: perspective(1000px) rotate3d(1, 0, 0, 0deg);
      }
      &::after {
        -webkit-transform: perspective(1000px) rotate3d(1, 0, 0, -90deg);
        transform: perspective(1000px) rotate3d(1, 0, 0, -90deg);
      }
    }
  }
}

%input__label--yoko {
  position: relative;
  width: 100%;
  color: #b04b40;
  text-align: left;
  &::before {
    content: '';
    position: absolute;
    bottom: 100%;
    left: 0;
    width: 100%;
    height: 4em;
    background: #c5564a;
    -webkit-transform: perspective(1000px) rotate3d(1, 0, 0, 90deg);
    transform: perspective(1000px) rotate3d(1, 0, 0, 90deg);
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 0.25em;
    background: #ad473c;
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
}

%input__label-content--yoko {
  padding: 0.75em 0;
}

/* Kyo */
%input--kyo {
  z-index: auto;
}

%input__field--kyo {
  padding: 0.85em 1.5em;
  width: 100%;
  border-radius: 2em;
  background: #fff;
  color: #535d92;
  &:focus {
    z-index: 10000;
    & + label {
      color: #fff;
      &::after {
        opacity: 1;
      }
    }
    & + label span {
      z-index: 10000;
    }
  }
}

%input__label--kyo {
  z-index: 0;
  padding: 0 0 0 2em;
  width: 100%;
  text-align: left;
  &::after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
    background: rgba(11, 43, 205, 0.6);
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    pointer-events: none;
  }
}

%input__label-content--kyo {
  padding: 0.5em 0;
}

/* Akira */
%input--akira {
  margin-top: 2em;
}

%input__field--akira {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  display: block;
  padding: 0 1em;
  width: 100%;
  height: 100%;
  background: transparent;
  text-align: center;
  &:focus, &.active {
    & + label {
      cursor: default;
      pointer-events: none;
      &::before {
        -webkit-transform: scale3d(0.99, 0.95, 1);
        transform: scale3d(0.99, 0.95, 1);
      }
    }
    & + label span {
      -webkit-transform: translate3d(0, -3.5em, 0);
      transform: translate3d(0, -3.5em, 0);
    }
  }
}

%input__label--akira {
  padding: 0;
  width: 100%;
  background: #696a6e;
  color: #cc6055;
  cursor: text;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #2f3238;
    -webkit-transform: scale3d(0.97, 0.85, 1);
    transform: scale3d(0.97, 0.85, 1);
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
}

%input__label-content--akira {
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}

/* Ichiro */
%input--ichiro {
  margin-top: 2em;
}

%input__field--ichiro {
  position: absolute;
  top: 4px;
  left: 4px;
  z-index: 100;
  display: block;
  padding: 0 0.55em;
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  background: #f0f0f0;
  color: #7F8994;
  opacity: 0;
  -webkit-transform: scale3d(1, 0, 1);
  transform: scale3d(1, 0, 1);
  -webkit-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s;
  &:focus, &.active {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
    & + label {
      cursor: default;
      pointer-events: none;
      &::before {
        -webkit-transform: scale3d(1, 1.5, 1);
        transform: scale3d(1, 1.5, 1);
      }
    }
    & + label span {
      -webkit-transform: translate3d(0, -3.15em, 0) scale3d(0.8, 0.8, 1);
      transform: translate3d(0, -3.15em, 0) scale3d(0.8, 0.8, 1) translateZ(1px);
    }
  }

}

%input__label--ichiro {
  width: 100%;
  text-align: left;
  cursor: text;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
}

%input__label-content--ichiro {
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}

/* Juro */
%input--juro {
  overflow: hidden;
}

%input__field--juro {
  position: absolute;
  z-index: 100;
  padding: 2.15em 0.75em 0;
  width: 100%;
  background: transparent;
  color: #1784cd;
  font-size: 0.85em;
  &:focus, &.active {
    & + label {
      &::before {
        border-width: 8px;
        border-color: #1784cd;
        border-top-width: 2em;
      }
    }
    & + label span {
      color: #fff;
      -webkit-transform: translate3d(0, -1.5em, 0) scale3d(0.75, 0.75, 1);
      transform: translate3d(0, -1.5em, 0) scale3d(0.75, 0.75, 1) translateZ(1px);
    }
  }
}

%input__label--juro {
  padding: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  text-align: left;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0px solid transparent;
    -webkit-transition: border-width 0.3s, border-color 0.3s;
    transition: border-width 0.3s, border-color 0.3s;
  }
}

%input__label-content--juro {
  padding: 2em 1em;
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-transition: -webkit-transform 0.3s, color 0.3s;
  transition: transform 0.3s, color 0.3s;

  text-rendering: geometricPrecision;
}

/* Kaede */
%input--kaede {
  display: block;
  overflow: hidden;
  margin: 1em auto 2em;
  background: #EFEEEE;
}

%input__field--kaede {
  position: absolute;
  top: 0;
  right: 100%;
  width: 60%;
  height: 100%;
  background: #fff;
  color: #9DABBA;
  -webkit-transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  &:focus, &.active {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    -webkit-transition-delay: 0.06s;
    transition-delay: 0.06s;
    & + label {
      -webkit-transform: translate3d(60%, 0, 0);
      transform: translate3d(60%, 0, 0);
      pointer-events: none;
    }
  }
}

%input__label--kaede {
  z-index: 10;
  display: block;
  width: 100%;
  height: 100%;
  text-align: left;
  cursor: text;
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}

%input__label-content--kaede {
  padding: 1.5em 0;
}

/* Isao */
%input--isao {
  visibility: inherit;
}

%input__field--isao {
  z-index: 10;
  padding: 0.75em 0.1em 0.25em;
  width: 100%;
  background: transparent;
  color: #afb3b8;
  &:focus {
    & + label {
      pointer-events: none;
      &::before {
        background-color: #da7071;
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
      }
      &::after {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }
    }
    & + label span {
      opacity: 0;
      -webkit-transform: translate3d(0, -50%, 0);
      transform: translate3d(0, -50%, 0);
    }
  }
}

%input__label--isao {
  position: relative;
  overflow: hidden;
  padding: 0;
  width: 100%;
  color: #dadada;
  text-align: left;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    width: 100%;
    height: 7px;
    background: #dadada;
    -webkit-transform: scale3d(1, 0.4, 1);
    transform: scale3d(1, 0.4, 1);
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    -webkit-transition: -webkit-transform 0.3s, background-color 0.3s;
    transition: transform 0.3s, background-color 0.3s;
    -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
    transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  }
  &::after {
    content: attr(data-content);
    position: absolute;
    top: 0;
    left: 0;
    padding: 0.75em 0.15em;
    color: #da7071;
    opacity: 0;
    -webkit-transform: translate3d(0, 50%, 0);
    transform: translate3d(0, 50%, 0);
    -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
    transition: opacity 0.3s, transform 0.3s;
    -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
    transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
    pointer-events: none;
  }
}

%input__label-content--isao {
  padding: 0.75em 0.15em;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s;
  -webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}

/* Yoshiko */
%input--yoshiko {
  margin-top: 1em;
}

%input__field--yoshiko {
  width: 100%;
  background-color: #d0d1d0;
  border: 2px solid transparent;
  -webkit-transition: background-color 0.3s, border-color 0.3s;
  transition: background-color 0.3s, border-color 0.3s;
  &:focus, &.active {
    background-color: transparent;
    border-color: #a3d39c;
    & + label {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
    & + label span {
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
    }
  }
}

%input__label--yoshiko {
  width: 100%;
  text-align: left;
  position: absolute;
  bottom: 100%;
  pointer-events: none;
  overflow: hidden;
  padding: 0 1.25em;
  -webkit-transform: translate3d(0, 3em, 0);
  transform: translate3d(0, 3em, 0);
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}

%input__label-content--yoshiko {
  color: #8B8C8B;
  padding: 0.25em 0;
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  &::after {
    content: attr(data-content);
    position: absolute;
    font-weight: 800;
    bottom: 100%;
    left: 0;
    height: 100%;
    width: 100%;
    color: #a3d39c;
    padding: 0.25em 0;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.85em;
  }
}

/* Nariko */
%input--nariko {
  overflow: hidden;
  padding-top: 2em;
}

%input__field--nariko {
  width: 100%;
  background: transparent;
  opacity: 0;
  padding: 0.35em;
  z-index: 100;
  color: #f18292;
  &:focus, &.active {
    cursor: text;
    opacity: 1;
    -webkit-transition: opacity 0s 0.3s;
    transition: opacity 0s 0.3s;
    & + label {
      &::before {
        -webkit-transition-delay: 0.1s;
        transition-delay: 0.1s;
        -webkit-transform: translate3d(0, -3.3em, 0);
        transform: translate3d(0, -3.3em, 0);
      }
    }
    & + label span {
      color: #6B6E6E;
      -webkit-transform: translate3d(0, -3.3em, 0) scale3d(0.81, 0.81, 1);
      transform: translate3d(0, -3.3em, 0) scale3d(0.81, 0.81, 1);
    }
  }
}

%input__label--nariko {
  width: 100%;
  bottom: 0;
  position: absolute;
  pointer-events: none;
  text-align: left;
  color: #8E9191;
  padding: 0 0.5em;
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 4em;
    top: 100%;
    left: 0;
    background: #fff;
    border-top: 4px solid #6B6E6E;
    -webkit-transform: translate3d(0, -3px, 0);
    transform: translate3d(0, -3px, 0);
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
  }
}



%input__label-content--nariko {
  padding: 0.5em 0;
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
  -webkit-transition: -webkit-transform 0.3s, color 0.3s;
  transition: transform 0.3s, color 0.3s;
}


/* Ruri */
%input--ruri {
  visibility: inherit;
  line-height: 1.5;
}

%input__field--ruri {
  width: 100%;
  background: transparent;
  padding: 0.5em 0;
  margin-bottom: 2em;
  color: #fff;
  &:focus, &.active {
    & + label {
      &::after {
        background: #a3d39c;
        -webkit-transform: scale3d(1, 0.25, 1);
        transform: scale3d(1, 0.25, 1);
      }
    }
    & + label span {
      color: #a3d39c;
      -webkit-transform: translate3d(0, 2em, 0) scale3d(0.655, 0.655, 1);
      transform: translate3d(0, 2.5em, 0) scale3d(0.655, 0.655, 1);
    }
  }
}

%input__label--ruri {
  width: 100%;
  position: absolute;
  text-align: left;
  font-size: 1em;
  padding: 10px 0 5px;
  pointer-events: none;
  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 7px;
    background: #6a7989;
    left: 0;
    top: 100%;
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    -webkit-transition: -webkit-transform 0.3s, background-color 0.3s;
    transition: transform 0.3s, background-color 0.3s;
  }
}

%input__label-content--ruri {
  padding: 0;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transition: -webkit-transform 0.3s, color 0.3s;
  transition: transform 0.3s, color 0.3s;
}
