
/*Sections*/
.about {
  background-color: #fff;
  position: relative;
  z-index: 2;
  text-align: center;
  &__header {
    position: relative;
    height: 100vh;
    max-height: 100vh;
    padding: 60px 5% 160px;
    background: url(./assets/img/content/img-card-19.jpg) 52% 10% no-repeat;
    background-size: cover;
    opacity: 0.95;
    color: #fff;
    text-shadow: 1px 1px 2px #727272;
  }
  &__write-up {
    @extend %container;
    width: 640px;
    position: relative;
    padding: 60px 10px 90px;
  }
  &__skills {
    position: relative;
    padding: 60px 0 90px;
    background-color: $color-light1;
  }
  &__social {
    @extend %container, %row, %pos-x-center-xs;
    position: relative;
    padding: 60px 0 90px;
    width: 800px;
  }
  &__img-cards {
    @extend %container-full, %row;
    text-align: left;
  }
  &__contact {
    @extend %row;
  }
}
/*sections end*/

/*contact btn*/
.contact-btn {
  @include btn-box($color-blue);
  margin: 1em 0;
}

/*Skills cards*/
.skills {
  @extend %container-fixed, %row;
  width: 960px;
  &__item {
    @extend %xs-12, %sm-6;
    border-color: transparent;
    background-color: transparent;
    padding: 75px 0;
    @include break(sm) {
      &:nth-child(odd) {
        border-right: 1px solid #d7d9da;
      }
      &:nth-child(1), &:nth-child(2) {
        border-bottom: 1px solid #d7d9da;
      }
    }
  }
  &__svg {
    display: block;
    width: 154px;
    height: 154px;
    position: absolute;
    top: 0;
    right: 50%;
    transform: translate(50%, 0);
    & {
      stroke: $color-pink;
      fill: transparent;
    }
    & .c1 {
      stroke-dasharray: 5.6, 5;
      stroke-width: .3;
    }
    & .c2 {
      stroke-dasharray: 0.45, 1.5;
      stroke-width: 1;
    }
    & .c3 {
      stroke-dasharray: 10, 6;
      stroke-width: .25;
    }
    & .c4 {
      stroke-dasharray: 0.2, 0.2;
      stroke-width: 0.2;
    }
  }
  &__icon {
    @include circle-icon(icon--ti-ruler-pencil, 154px, 54px, transparent, $color-pink);
    @include circle-icon(icon--md-cutlery, $copy: 1);
    @include circle-icon(icon--fa-lightbulb-o, $copy: 1);
    @include circle-icon(icon--fa-code, $copy: 1);
    box-sizing: content-box;
    border: 3px solid;
    position: relative;
  }
}

/*Social icons*/
.social-icon {
  @include circle-icon(icon--md-facebook, 140px, 2.75em, transparent, $color-light2);
  @include circle-icon(icon--md-twitter, $copy: 1);
  @include circle-icon(icon--md-linkedin, $copy: 1);
  @include circle-icon(icon--md-github, $copy: 1);
  border: 1px solid;
  transition: all 0.4s;
  margin: 10px;
  @include hover-focus {
    background-color: $color-light2;
    color: #fff;
  }
}

/*Image Cards*/
.about__img-cards {
  .item-row {
    @extend %xs-12, %sm-9, %row;
  }
  span {
    @extend %xs-12;
    padding: 0 !important;
    &.item {
      &-1 {
        @extend %sm-3;
      }
      &-2 {
        @extend %sm-8;
      }
      &-3 {
        @extend %sm-4;
      }
      &-4 {
        @extend %sm-4;
      }
      &-5 {
        @extend %sm-4;
      }
      &-6 {
        @extend %sm-4;
      }
      &-7 {
        @extend %sm-6;
      }
      &-8 {
        @extend %sm-3;
      }
      &-9 {
        @extend %sm-3;
      }
    }
  }
  figure {
    background-color: $color-dark;
    overflow: hidden;
    transition: all 350ms cubic-bezier(.215, .61, .215, 1);
    display: block;
    width: 100%;
    height: 100%;
    position: relative;
    line-height: 0;
    @include hover-focus-active {
      figcaption {
        bottom: 0;
      }
      img {
        transform: scale(1.05);
        opacity: .5;
      }
    }
    figcaption {
      font-size: 2em;
      font-weight: 400;
      font-family: Lora, serif;
      font-style: italic;
      line-height: 1em;
      text-transform: none;
      transition: all 350ms cubic-bezier(.215, .61, .215, 1);
      z-index: 6500;
      position: absolute;
      display: block;
      bottom: -100%;
      left: 0;
      padding: 30px;
      color: #fff;
    }
    img {
      transition: all 350ms cubic-bezier(.215, .61, .215, 1);
      width: 100%;
      max-width: 100%;
    }
  }
}

/* Contact & Webdesign links*/
.contact, .webdesign {
  &__link {
    @extend %xs-12, %sm-6, %no-padding, .fadeInUp;
    @include btn-box($color-blue);
    height: 200px;
    border: 3px transparent;
    overflow: hidden;
    &:nth-child(2) {
      border-left: 1px solid $color-light2;
      & > span::before {
        background-color: $color-green;
      }
    }
    &::after {
      background-color: transparent;
    }
    & > h4 {
      line-height: 160px;
      margin: 0;
      transition: color 0.3s;
    }
    & > span {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
    @include hover-focus-active {
      letter-spacing: .4em;
      & > h4 {
        color: #fff;
      }
    }
  }
}

.footer {
  @extend %container-full, %row, %pos-x-center-xs;
  text-align: center;
  background-color: $color-dark;
  bottom: 0;
  width: 100%;
  position: fixed;
  z-index: -9;
  padding: 150px 0;
  color: #fff;
  blockquote {
    @extend %xs-12;
  }
  .social-icon {
    @include circle-icon(icon--md-facebook, 65px, 1.25em, transparent, #fff);
    @include circle-icon(icon--md-twitter, $copy: 1);
    @include circle-icon(icon--md-linkedin, $copy: 1);
    @include circle-icon(icon--md-github, $copy: 1);
    border-radius: 0;
    border: 1px solid transparent;
    transition: all 0.4s;
    margin: 5px;
    @include hover-focus {
      background-color: $color-pink;
      color: #fff;
      border-radius: 50%;
    }
  }
}

.footer-space {
  height: 463px;
  position: relative;
  z-index: -10;
  pointer-events: none;
}

.back-top {
  bottom: 30px;
  position: fixed;
  right: 30px;
  transition: all 350ms cubic-bezier(.215, .61, .215, 1);
  z-index: 2000;
  &__link {
    @include circle-icon(icon--fa-angle-double-up, 80px, 2.75em, $color-light1, $color-dark);
    margin: 0;
    box-sizing: content-box;
    line-height: inherit;
    overflow: hidden;
    text-decoration: none;
    transition: all 450ms cubic-bezier(.215, .61, .215, 1);
    border: 1px solid $color-light2;
    @include hover-focus-active {
      background-color: $color-dark;
      color: #fff;
    }
  }
}



















