
.nav-icon {
  position: fixed;
  z-index: 2100;
  top: 25px;
  right: 15px;
  width: 30px;
  height: 40px;
  padding: 0;

  transition: .5s ease-in-out;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  // CSS Burger Bar
  &__bar {
    display: block;
    position: absolute;
    left: 0;
    height: 3px;
    width: 100%;

    background: #252627;
    opacity: 1;
    transition: .2s;
    &:nth-child(1) {
      top: 0;
    }
    &:nth-child(2),
    &:nth-child(3) {
      top: 10px;
    }
    &:nth-child(4) {
      top: 20px;
    }
  }
  // X Animation
  &.active {
    .nav-icon__bar {
      background-color: #fff;
      &:nth-child(1) {
        top: 10px;
        width: 0;
        left: 50%
      }
      &:nth-child(2) {
        transform: rotate(45deg);
      }
      &:nth-child(3) {
        transform: rotate(-45deg);
      }
      &:nth-child(4) {
        top: 10px;
        width: 0;
        left: 50%;
      }
    }
  }
}

@include break(md) {
  .nav-icon {
    top: 45px;
    right: 30px;
    width: 50px;
    padding: 30px 0;
    &__bar {
      &:nth-child(1) {
        top: 8px;
      }
      &:nth-child(2),
      &:nth-child(3) {
        top: 22px;
      }
      &:nth-child(4) {
        top: 36px;
      }
    }
    &.active {
      .nav-icon__bar {
        &:nth-child(1) {
          top: 16px;
        }
        &:nth-child(4) {
          top: 18px;
        }
      }
    }
  }
}

@include break(lg) {
  #nav-icon {
    top: 52px;
    right: 45px;
  }
}




