/*Sections*/
.webdesign {
  background-color: #fff;
  position: relative;
  z-index: 2;
  &__header {
    padding: 60px 5% 60px;
    em {
      letter-spacing: -0.063em;
    }
  }
  &__steps {
    div {
      @extend %container, %row;
      max-width: 960px;
      position: relative;
      padding: 30px 0 90px;
    }
    header {
      @extend %xs-12, %md-5, %lg-4;
    }
    article {
      @extend %xs-12, %md-7, %lg-8;
    }
  }
}

.step-2 {
  header {
    order: -1;
  }
}







