.nav {
  // NAV DEFAULT STYLES
  display: block;
  position: fixed;
  z-index: 2050;
  top: -150%;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;

  background: url(./assets/img/pattern-bg/bg-pattern.png) rgba(37, 38, 39, .97);
  font-weight: 700;
  font-size: 1.75em;
  text-align: left;
  text-transform: uppercase;
  transition: .5s;
  &__list {
    position: relative;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: transparent;
  }
  &__item {
    display: block;
    position: absolute;
    top: -150%;
    width: 100%;
    margin: 0;
    padding: 0;
    opacity: 0;

    text-align: center;
    letter-spacing: .2em;
    line-height: 2em;
    font-weight: 700;
    transition: all 300ms cubic-bezier(.235, .625, .07, .98);
    &--social {
      display: block;
      position: absolute;
      width: 100%;
      top: 200%;
      margin: 0;
      padding: 0;

      opacity: 0;
      color: #6f7276;
      font-size: .4em;
      letter-spacing: .1em;
      font-weight: 400;
      text-align: center;
      line-height: 2em;
      transition: all 300ms cubic-bezier(.235, .625, .07, .98);
    }
  }
  &__link {
    position: relative;
    z-index: 8888;
    margin: 0;
    padding: 0;
    border: none;
    overflow: visible;

    background-color: transparent;
    color: #fff;
    text-decoration: none;
    transition: all 350ms cubic-bezier(.215, .61, .215, 1);
    @include hover-focus-active {
      letter-spacing: .4em;
      color: $color-blue;
    }
    &--icon {
      @extend .nav__link;
      display: inline-block;
      margin: 0;
      font-size: 2.25em;
      letter-spacing: 0;
      @include circle-icon(icon--md-facebook, 60px, 2.75em, transparent, rgba(255, 255, 255, .5));
      @include circle-icon(icon--md-twitter, $copy: 1);
      @include circle-icon(icon--md-linkedin, $copy: 1);
      @include circle-icon(icon--md-github, $copy: 1);
      border-radius: 0;
    }
  }

  // NAV OPEN STYLES
  &.active {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition-delay: .15s;
    .nav__item {
      opacity: 1;
      &:nth-child(1) {
        transition-delay: .84s;
        top: 12%
      }
      &:nth-child(2) {
        transition-delay: .78s;
        top: 22%
      }
      &:nth-child(3) {
        transition-delay: .72s;
        top: 32%
      }
      &:nth-child(4) {
        transition-delay: .66s;
        top: 42%
      }
      &:nth-child(5) {
        transition-delay: .6s;
        top: 52%
      }
      &--social {
        opacity: 1;
        transition-delay: 1.1s;
        top: 67%;
      }
    }
    .nav__link--icon {
      transition-delay: 0s;
      color: #fff;
      @include hover-focus-active {
        border-radius: 50%;
        background-color: $color-blue;
        letter-spacing: 0;
      }
    }
  }
}

.cl-effect-3 a::before {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: -300%;
  height: 2px;
  width: 200%;
  background-color: $color-blue;
  opacity: 0;
  transition: all 350ms cubic-bezier(.215, .61, .215, 1);
}

@include break(md) {
  .nav {
    font-size: 2.25em;
    &__link {
      &::before {
        content: "";
        display: block;
        position: absolute;
        z-index: -1;
        top: 50%;
        left: -300%;
        height: 2px;
        width: 200%;
        background-color: $color-blue;
        opacity: 0;
        transition: all 350ms cubic-bezier(.215, .61, .215, 1);
      }
      @include hover-focus-active-class {
        &::before {
          width: 110%;
          opacity: 1;
          left: -10%;
        }
      }
      &--icon {
        font-size: 2.25em;
        &::before {
          content: none;
        }
      }
    }
  }
}


