body::before {
  content: "";
  display: block;
  position: fixed;
  z-index: 5000;
  top: -5px;
  left: -237%;
  width: 200%;
  height: 110%;

  background: $bg-blue-gradient;
  opacity: 1;

  overflow: hidden;
  transform: skew(-24deg, 0);
  transition: 1.2s;
  transition-delay: .2s;
}

.loading {
  &::before {
    left: -50%;
    transition: 1.2s;
  }
  &__spinner {
    opacity: 0;
    top: -250px;
    position: fixed;
    z-index: 5001;
    left: 50%;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    transition: .5s;
    &__img {
      display: block;
      width: 76px;
      margin: 87px
    }
    &__animation {
      position: absolute;
      top: 60px;
      right: 60px;
      bottom: 60px;
      left: 60px;
      border: 4px solid #fff;
      opacity: 1;
      &:nth-child(2) {
        transform: rotate(6deg);
        animation: rotation1 3s infinite;
      }
      &:nth-child(3) {
        transform: rotate(19deg);
        animation: rotation2 3s infinite;;
        animation-delay: 0.2s;
      }
    }
  }
  & &__spinner {
    top: 50%;
    opacity: 1;
  }
}

/* first rotation */
@keyframes rotation1 {
  from {
    transform: rotate(-366deg);
  }
  30%, 50% {
    transform: rotate(45deg);
  }
  70% {
    transform: rotate(6deg);
  }
}

/* second rotation */
@keyframes rotation2 {
  from {
    transform: rotate(-379deg);
  }
  30%, 40% {
    transform: rotate(150deg);
  }
  80% {
    transform: rotate(19deg);
  }
}


// TODO: Look at usefulness of wrapper & add no-js functionality!
.loaded main.wrapper {
  opacity: 1;
  min-height: 100%;
  height: auto
}



.no-js #loader-wrapper {
  display: none
}
