@if ($enable-section-transition == true) {
  .transition {
    &__wrapper {
      display: block;
      position: relative;
      min-height: 100vh;
      width: 100%;
      perspective: 1200px;
      overflow: hidden;
      background: url(./assets/img/pattern-bg/bg-pattern.png) #3e4041;
      text-align: center;
    }
    &__section {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      visibility: hidden;
      overflow: hidden;
      background-color: #fff;
      backface-visibility: hidden;
      transform: translate3d(0, 0, 0);
      transform-style: preserve-3d;
      &.active {
        visibility: visible;
        z-index: 1;
      }
    }
    &__trigger {
      position: absolute;
      z-index: 5999;
      left: 50%;
      padding: 30px 45px;
      cursor: pointer;
      text-align: center;
      text-transform: uppercase;
      font-size: .75em;
      letter-spacing: .2em;
      color: rgba(37, 38, 39, .5);
      transform: translate(-50%, 0);
      transition: all 350ms cubic-bezier(.215, .61, .215, 1);
      &__icon {
        transition: all 350ms cubic-bezier(.215, .61, .215, 1);
        transform: translate(-50%, 0);
      }
      &:hover {
        color: #252627
      }
    }
    &__up {
      top: 5px;
      & .transition__trigger__icon {
        @extend %icon--md-caret-up;
        position: absolute;
        top: -100px;
      }
      &:hover {
        padding-top: 60px;
        & .transition__trigger__icon {
          top: 30px
        }
      }
    }
    &__down {
      bottom: 5px;
      & .transition__trigger__icon {
        @extend %icon--md-caret-down;
        position: absolute;
        bottom: -100px;
      }
      &:hover {
        padding-bottom: 60px;
        & .transition__trigger__icon {
          bottom: 30px;
        }
      }
    }
  }

  .et-page-current,
  .no-js .et-page {
    visibility: visible;
    z-index: 1;
  }

  .transition--moveToLeft {
    -webkit-animation: moveToLeft .6s ease both;
    -moz-animation: moveToLeft .6s ease both;
    animation: moveToLeft .6s ease both;
  }

